import axios from './index'

const api = {
    userBaseInfo: '/userBasicInfo',
    userBase: '/user/',
    publicBase: "/public",
}

export default api


export function initUserByCsv(filename) {
    return axios({
        url: api.userBase + "initDataByCsv/" + filename,
        method: 'get',
    })
}

export function addUserByCsv(filename) {
    return axios({
        url: api.userBase + "addDataByCsv/" + filename,
        method: 'get',
    })
}

export function addUserPairByCsv(filename) {
    return axios({
        url: api.userBase + "addPairByCsv/" + filename,
        method: 'get',
    })
}

export function downloadReport() {
    return axios({
        url: api.userBase + "downloadReport",
        method: 'get',
    })
}

export function getUserBaseInfo(data) {
    return axios({
        url: api.userBaseInfo,
        method: 'post',
        data: data,
    })
}

export function getPublicInfo(gender) {
    //console.log(api.publicBase + "info/" + gender);
    return axios({
        url: api.publicBase + "info/" + gender,
        method: 'get',
    })
}

export function getPublicInfoWithPage(gender, page) {
    //console.log(api.publicBase + "info/" + gender);
    return axios({
        url: api.publicBase + "info/" + gender + "/" + page,
        method: 'get',
    })
}

