export function sendEmail(data) {
    return axios({
        url: api.userBase + "sendEmail",
        method: 'post',
        data: data,
    })
}

export function updateUser(id, data) {
    return axios({
        url: api.userBase + id,
        method: 'post',
        data: data,
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    })
}


