        var elInput = document.getElementById('emojiInput') // id
        var startPos = elInput.selectionStart// input 0
        var endPos = elInput.selectionEnd// 
        if (startPos === undefined || endPos === undefined) return
        var txt = elInput.value
        // 
        var result = txt.substring(0, startPos) + item + txt.substring(endPos)
        elInput.value = result// inputvalue
        // 
        elInput.focus()
        elInput.selectionStart = startPos + item.length
        elInput.selectionEnd = startPos + item.length
        this.someNotiForm.content = result// 
